.weather{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px;
    border-radius: 10px;
    background-image: linear-gradient(45deg,#4281A4,#336699);
}


.search-bar{
    display: flex;
    align-items: center;
    gap: 12px;
}

.search-bar input{
    height: 50px;
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 25px;
    font-size: 18px;
    background: whitesmoke;
}

.search-bar img{
    width: 50px;
    padding: 15px;
    border-radius: 50%;
    background: whitesmoke;
    cursor: pointer;
}

.weather-icon{
    width: 150px;
    margin: 30px 0;
}

.temperature{
    font-size: 80px;
    line-height: 1;
    color: white;
}

.location{
    color: white;
    font-size: 40px;

}

.weather-data{
    width: 100%;
    margin-top: 40px;
    color: white;
    display: flex;
    justify-content: space-between;
    gap: 40px;
}

.weather-data .col{
    display: flex;
    gap: 12px;
    font-size: 25px;
    align-items: center;
    
}

.weather-data .col span{
    display: block;
    font-size: 16px;
}
.weather-data .col img{
    width: 50px;
    
}